.footer {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: auto;
    position: relative;
    flex-shrink: 0;
    height: auto;
    width: 100%;
    background-color: var(--footer-background-color);
}

.footer-container {
    position: relative;
    height: 200px;
    width: var(--container-width);
}