.catalog-product {
    overflow: visible visible;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: var(--medium-spacing) 0;
    padding: var(--medium-spacing) var(--medium-spacing);
    position: relative;
    aspect-ratio: 1 / 1;
    width: 100%;
    background-color: var(--element-background-color);
    border-radius: var(--medium-border-radius);

    & > img {
        box-sizing: border-box;
        position: relative;
        object-fit: cover;
        height: 100%;
        width: 100%;
        border-radius: var(--medium-border-radius);
        filter: brightness(70%);
        transition: 0.2s;
    }

    &.added {
        & > img {
            border-radius: var(--medium-border-radius);
            filter: brightness(100%);
            transition: 0.1s;
        }
    }

    &:hover {
        & > img {
            border-radius: var(--medium-border-radius);
            filter: brightness(100%);
            transition: 0.1s;
        }
    }
}

.catalog-product-panel {
    overflow: hidden hidden;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    gap: var(--small-spacing) 0;
    position: relative;
    height: auto;
    width: 100%;
    border-radius: 0 0 var(--medium-border-radius) var(--medium-border-radius);
}

.catalog-product-panel-brand {
    overflow: hidden hidden;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    gap: var(--small-spacing) var(--small-spacing);
    position: relative;
    height: auto;
    width: 100%;
    color: var(--primary-text-color);
    font-size: var(--medium-font-size);
    font-weight: var(--large-font-weight);
    font-family: var(--primary-font-family);
    user-select: text;
}

.catalog-product-panel-brand-strength {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    gap: 0 var(--tiny-spacing);
    padding: var(--tiny-spacing) var(--small-spacing);
    position: relative;
    height: auto;
    width: auto;
    font-size: var(--small-font-size);
    font-weight: var(--large-font-weight);
    font-family: var(--primary-font-family);
    border-radius: var(--small-border-radius);
    user-select: none;

    &.insane {
        color: rgba(220, 40, 220, 1);
        background-color: rgba(220, 40, 220, 0.2);
    }

    &.strong {
        color: rgba(240, 70, 40, 1);
        background-color: rgba(240, 70, 40, 0.2);
    }

    &.default {
        color: rgba(240, 140, 40, 1);
        background-color: rgba(240, 140, 40, 0.2);
    }

    &.low {
        color: rgba(70, 240, 40, 1);
        background-color: rgba(70, 240, 40, 0.2);
    }
}

.catalog-product-panel-brand-strength-type {
    text-transform: uppercase;
}

.catalog-product-panel-name {
    overflow: hidden hidden;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: start;
    gap: var(--small-spacing) var(--small-spacing);
    margin-bottom: var(--small-spacing);
    position: relative;
    height: auto;
    width: 100%;
    color: var(--secondary-text-color);
    font-size: var(--medium-font-size);
    font-weight: var(--primary-font-weight);
    font-family: var(--primary-font-family);
    user-select: text;
}

.catalog-product-panel-price {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    gap: var(--medium-spacing) var(--medium-spacing);
    padding: var(--medium-spacing) var(--large-spacing);
    margin-top: auto;
    position: relative;
    height: auto;
    width: 100%;
    background-color: var(--element-transparent-background-color);
    border-radius: var(--small-border-radius);
    cursor: pointer;
}

.catalog-product-panel-price-value {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: end;
    gap: 0 var(--tiny-spacing);
    position: relative;
    height: auto;
    width: auto;
    color: var(--primary-text-color);
    font-size: var(--primary-font-size);
    font-weight: var(--huge-font-weight);
    font-family: var(--primary-font-family);
}

.catalog-product-panel-price-value-currency {
    box-sizing: border-box;
    padding-bottom: 1px;
    position: relative;
    height: auto;
    width: 100%;
    color: var(--secondary-text-color);
    font-size: var(--medium-font-size);
    font-weight: var(--huge-font-weight);
    font-family: var(--primary-font-family);
}

.catalog-product-panel-price-add_to_cart {
    margin-left: auto;
    position: relative;
    height: auto;
    width: auto;
    color: var(--primary-text-color);
    font-size: var(--primary-font-size);
}