.cart-details {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    gap: var(--medium-spacing) 0;
    padding: var(--medium-spacing) var(--medium-spacing);
    margin-left: auto;
    position: relative;
    flex-shrink: 0;
    height: auto;
    flex: 1 1 0;
    min-width: 320px;
    max-width: 100%;
    background-color: var(--element-background-color);
    border-radius: var(--medium-border-radius);
}

.cart-details-address {
    box-sizing: border-box;
    padding: var(--primary-spacing) var(--primary-spacing);
    position: relative;
    height: auto;
    width: 100%;
    color: var(--primary-text-color);
    font-size: var(--medium-font-size);
    font-weight: var(--large-font-weight);
    font-family: var(--primary-font-family);
    background-color: var(--element-background-color);
    border: var(--medium-border-size) solid var(--element-transparent-background-color);
    border-radius: var(--medium-border-radius);
    outline: none;
}

.cart-details-promocode {
    box-sizing: border-box;
    padding: var(--primary-spacing) var(--primary-spacing);
    position: relative;
    height: auto;
    width: 100%;
    color: var(--primary-text-color);
    font-size: var(--medium-font-size);
    font-weight: var(--large-font-weight);
    font-family: var(--primary-font-family);
    background-color: var(--element-background-color);
    border: var(--medium-border-size) solid var(--element-transparent-background-color);
    border-radius: var(--medium-border-radius);
    outline: none;
}

.cart-details-payment {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0 var(--small-spacing);
    position: relative;
    height: auto;
    width: 100%;
}

.cart-details-payment-method {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: var(--medium-spacing) 0;
    padding: var(--medium-spacing) var(--medium-spacing);
    position: relative;
    height: auto;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.01);
    border: var(--medium-border-size) solid transparent;
    border-radius: var(--medium-border-radius);
    cursor: pointer;
    transition: 0.2s;

    & > i {
        color: var(--secondary-text-color);
        font-size: var(--huge-font-size);
    }

    & > span {
        text-align: center;
        color: var(--primary-text-color);
        font-size: var(--medium-font-size);
        font-weight: var(--large-font-weight);
        font-family: var(--primary-font-family);
    }

    &.selected {
        border: var(--medium-border-size) solid var(--primary-color);
        transition: 0.2s;
    }
}

.cart-details-order {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: start;
    align-items: center;
    gap: var(--medium-spacing) var(--small-spacing);
    padding: var(--medium-spacing) var(--medium-spacing);
    position: relative;
    height: auto;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.01);
    border-radius: var(--medium-border-radius);

    & > span {
        color: var(--secondary-text-color);
        font-size: var(--medium-font-size);
        font-weight: var(--large-font-weight);
        font-family: var(--primary-font-family);
    }
}

.cart-details-order-price {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: end;
    gap: 0 calc(var(--tiny-spacing) - 1px);
    margin-left: auto;
    position: relative;
    height: auto;
    width: auto;
}

.cart-details-order-price-value {
    position: relative;
    height: auto;
    width: auto;
    color: var(--primary-text-color);
    font-size: var(--medium-font-size);
    font-weight: var(--large-font-weight);
    font-family: var(--primary-font-family);
}

.cart-details-order-price-currency {
    box-sizing: border-box;
    padding-bottom: 1px;
    position: relative;
    height: auto;
    width: auto;
    color: var(--secondary-text-color);
    font-size: var(--small-font-size);
    font-weight: var(--large-font-weight);
    font-family: var(--primary-font-family);
}

.cart-details-order-button {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    gap: 0 var(--medium-spacing);
    padding: var(--medium-spacing) var(--primary-spacing);
    position: relative;
    height: auto;
    width: 100%;
    color: var(--invert-text-color);
    font-size: var(--medium-font-size);
    font-weight: var(--large-font-weight);
    font-family: var(--primary-font-family);
    background-color: var(--primary-color);
    border-radius: var(--absolute-border-radius);
    cursor: pointer;
}