.header {
    overflow: hidden hidden;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: start;
    align-items: center;
    gap: 0 var(--medium-spacing);
    padding: var(--medium-spacing) var(--medium-spacing);
    position: fixed;
    bottom: -78px;
    left: 50%;
    height: auto;
    width: calc(var(--container-width) - (var(--primary-spacing) * 2));
    max-width: calc(100% - (var(--primary-spacing) * 2));
    backdrop-filter: blur(var(--header-blur));
    background-color: var(--header-background-color);
    border-radius: var(--small-border-radius);
    transform: translateX(-50%) scale(80%);
    z-index: 2;
    transition: 0.5s;

    &.shown {
        bottom: var(--primary-spacing);
        transform: translateX(-50%) scale(100%);
        transition: 0.5s;
    }
}

.header-account {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: start;
    align-items: center;
    gap: 0 var(--medium-spacing);
    padding: var(--small-spacing) calc(var(--small-spacing) * 2) var(--small-spacing) var(--small-spacing);
    position: relative;
    height: auto;
    width: auto;
    max-width: 250px;
    background-color: var(--element-transparent-background-color);
    border-radius: var(--absolute-border-radius);
}

.header-account-avatar {
    overflow: hidden hidden;
    position: relative;
    height: 30px;
    width: 30px;
    border-radius: var(--absolute-border-radius);

    & > img {
        position: relative;
        object-fit: cover;
        height: 100%;
        width: 100%;
        border-radius: var(--absolute-border-radius);
    }
}

.header-account-name {
    position: relative;
    height: auto;
    width: auto;
    color: var(--primary-text-color);
    font-size: var(--medium-font-size);
    font-weight: var(--large-font-weight);
    font-family: var(--primary-font-family);
}

.header-cart {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    gap: 0 var(--large-spacing);
    margin-left: auto;
    position: relative;
    height: auto;
    width: auto;
}

.header-cart-price {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: end;
    gap: 0 calc(var(--tiny-spacing) - 1px);
    position: relative;
    height: auto;
    width: auto;
}

.header-cart-price-value {
    position: relative;
    height: auto;
    width: auto;
    color: var(--primary-text-color);
    font-size: var(--medium-font-size);
    font-weight: var(--large-font-weight);
    font-family: var(--primary-font-family);
}

.header-cart-price-currency {
    box-sizing: border-box;
    padding-bottom: 1px;
    position: relative;
    height: auto;
    width: auto;
    color: var(--secondary-text-color);
    font-size: var(--small-font-size);
    font-weight: var(--large-font-weight);
    font-family: var(--primary-font-family);
}

.header-cart-button {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    gap: 0 var(--medium-spacing);
    padding: var(--medium-spacing) var(--primary-spacing);
    position: relative;
    height: 42px;
    width: auto;
    color: var(--invert-text-color);
    font-size: var(--medium-font-size);
    font-weight: var(--large-font-weight);
    font-family: var(--primary-font-family);
    background-color: var(--primary-color);
    border-radius: var(--absolute-border-radius);
    cursor: pointer;
}

@media (max-width: 600px) {
    .header-cart-button {
        & > span {
            display: none;
        }
    }
}