@keyframes marching-ants-big {
    0% {
        background-position:
            0 0,
            100% 0,
            100% 100%,
            0 100%;
    }
    100% {
        background-position:
            48px 0,
            100% 48px,
            calc(100% - 48px) 100%,
            0 calc(100% - 48px);
    }
}

@keyframes marching-ants-small {
    0% {
        background-position:
            0 0,
            100% 0,
            100% 100%,
            0 100%;
    }
    100% {
        background-position:
            24px 0,
            100% 24px,
            calc(100% - 24px) 100%,
            0 calc(100% - 24px);
    }
}

.catalog-filters {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    gap: var(--medium-spacing) 0;
    position: relative;
    height: auto;
    width: 100%;
}

.catalog-filters-type-list {
    overflow: scroll hidden;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
    grid-auto-columns: minmax(160px, 1fr);
    grid-auto-flow: column;
    gap: var(--medium-spacing) var(--medium-spacing);
    position: relative;
    height: auto;
    width: 100%;
    scrollbar-width: none;
    -ms-overflow-style: none;

    &::-webkit-scrollbar {
        display: none;
    }
}

.catalog-filters-type {
    overflow: hidden hidden;
    box-sizing: border-box;
    position: relative;
    aspect-ratio: 1 / 1;
    background-color: var(--element-background-color);
    border: var(--small-border-size) solid transparent;
    border-radius: var(--small-border-radius);
    transition: 0.2s;

    & > img {
        position: relative;
        object-fit: cover;
        height: 100%;
        width: 100%;
        border-radius: var(--small-border-radius);
        filter: brightness(50%);
        transition: 0.2s;
    }

    &.selected {
        & > img {
            filter: brightness(100%);
            transform: scale(110%);
            transition: 0.2s;
        }

        .catalog-filters-type-border {
            opacity: 1;
            transition: 0.5s;
        }
    }

    &.applied {
        & > img {
            filter: brightness(100%);
        }

        border: var(--medium-border-size) solid var(--primary-color);
        transition: 0.2s;
    }
}

.catalog-filters-type-border {
    box-sizing: border-box;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background:
        linear-gradient(90deg, transparent 50%, var(--primary-color) 0) repeat-x,
        linear-gradient(0deg, transparent 50%, var(--primary-color) 0) repeat-y,
        linear-gradient(90deg, var(--primary-color) 50%, transparent 0) repeat-x,
        linear-gradient(0deg, var(--primary-color) 50%, transparent 0) repeat-y;
    background-size:
        48px var(--medium-border-size),
        var(--medium-border-size) 48px,
        48px var(--medium-border-size),
        var(--medium-border-size) 48px;
    background-position:
        0 0,
        100% 0,
        100% 100%,
        0 100%;
    background-repeat: repeat-x, repeat-y, repeat-x, repeat-y;
    animation: marching-ants-big 1s linear infinite;
    opacity: 0;
}

.catalog-filters-brand-list {
    overflow: scroll hidden;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: start;
    align-items: center;
    gap: 0 var(--small-spacing);
    position: relative;
    height: auto;
    width: 100%;
    scrollbar-width: none;
    -ms-overflow-style: none;

    &::-webkit-scrollbar {
        display: none;
    }
}

.catalog-filters-brand {
    overflow: hidden hidden;
    box-sizing: border-box;
    position: relative;
    flex-shrink: 0;
    height: 80px;
    width: 80px;
    background-color: var(--element-background-color);
    border-radius: var(--small-border-radius);

    & > img {
        position: relative;
        object-fit: cover;
        height: 100%;
        width: 100%;
        border-radius: var(--small-border-radius);
        filter: brightness(50%);
        transition: 0.2s;
    }

    &.selected {
        & > img {
            filter: brightness(100%);
            transform: scale(110%);
            transition: 0.2s;
        }

        .catalog-filters-brand-border {
            opacity: 1;
            transition: 0.5s;
        }
    }

    &.applied {
        & > img {
            filter: brightness(100%);
        }

        border: var(--medium-border-size) solid var(--primary-color);
        transition: 0.2s;
    }
}

.catalog-filters-brand-border {
    box-sizing: border-box;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background:
        linear-gradient(90deg, transparent 50%, var(--primary-color) 0) repeat-x,
        linear-gradient(0deg, transparent 50%, var(--primary-color) 0) repeat-y,
        linear-gradient(90deg, var(--primary-color) 50%, transparent 0) repeat-x,
        linear-gradient(0deg, var(--primary-color) 50%, transparent 0) repeat-y;
    background-size:
        24px var(--medium-border-size),
        var(--medium-border-size) 24px,
        24px var(--medium-border-size),
        var(--medium-border-size) 24px;
    background-position:
        0 0,
        100% 0,
        100% 100%,
        0 100%;
    background-repeat: repeat-x, repeat-y, repeat-x, repeat-y;
    animation: marching-ants-small 1s linear infinite;
    opacity: 0;
}

.catalog-filters-actions {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: start;
    align-items: center;
    gap: var(--small-spacing) var(--small-spacing);
    position: relative;
    height: auto;
}

.catalog-filters-action {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    gap: 0 var(--medium-spacing);
    padding: var(--medium-spacing) var(--primary-spacing);
    position: relative;
    height: auto;
    width: auto;
    color: var(--primary-text-color);
    font-size: var(--medium-font-size);
    font-weight: var(--large-font-weight);
    font-family: var(--primary-font-family);
    background-color: var(--element-background-color);
    border-radius: var(--absolute-border-radius);
    cursor: pointer;
}

.catalog-filters-actions-reset {
    background-color: var(--element-transparent-background-color);
}

.catalog-filters-actions-apply {
    color: var(--invert-text-color);
    background-color: var(--primary-color);
}

@media (max-width: 800px) {
    .catalog-filters-actions {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: var(--medium-spacing) var(--medium-spacing);
        width: 100%;
    }

    .catalog-filters-action {
        width: 100%;
        min-width: 0;
    }
}

@media (max-width: 400px) {
    .catalog-filters-actions {
        display: flex;
        flex-direction: column;
        grid-template-columns: 1fr;
        gap: var(--small-spacing) 0;
    }

    .catalog-filters-action {
        width: 100%;
    }
}