.catalog-products {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    gap: var(--medium-spacing) var(--medium-spacing);
    position: relative;
    height: 100%;
    width: 100%;
}

@media (max-width: 675px) {
    .catalog-products {
        grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
    }
}