:root {
    --container-width: 1440px;
    --spacer-height: 240px;
    --bottom-distance: 0px;

    --tiny-spacing: 3px;
    --small-spacing: 6px;
    --medium-spacing: 12px;
    --primary-spacing: 18px;
    --large-spacing: 24px;
    --huge-spacing: 30px;

    --small-font-size: 12px;
    --medium-font-size: 15px;
    --primary-font-size: 18px;
    --large-font-size: 24px;
    --huge-font-size: 30px;

    --small-font-weight: 100;
    --medium-font-weight: 300;
    --primary-font-weight: 500;
    --large-font-weight: 700;
    --huge-font-weight: 900;

    --small-border-size: 1px;
    --medium-border-size: 2px;
    --primary-border-size: 3px;
    --large-border-size: 4.5px;
    --huge-border-size: 6px;

    --small-border-radius: 6px;
    --medium-border-radius: 12px;
    --primary-border-radius: 18px;
    --large-border-radius: 24px;
    --huge-border-radius: 30px;
    --absolute-border-radius: 9999px;

    --header-blur: 12px;

    --primary-font-family: "Mulish";
}

body {
    overflow: hidden scroll;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    padding: 0;
    margin: 0;
    height: 100dvh;
    width: 100%;
    background-color: var(--body-background-color);
    user-select: none;
}

#root {
    overflow: hidden scroll;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    position: relative;
    flex-shrink: 0;
    height: 100%;
    width: 100%;
}