.cart-product {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: start;
    align-self: center;
    gap: var(--medium-spacing) var(--medium-spacing);
    padding: var(--medium-spacing) var(--medium-spacing);
    position: relative;
    height: auto;
    width: 100%;
    background-color: var(--element-background-color);
    border-radius: var(--medium-border-radius);
}

.cart-product-preview {
    overflow: hidden hidden;
    position: relative;
    height: 132px;
    width: 132px;
    background-color: var(--element-background-color);
    border-radius: var(--medium-border-radius);

    & > img {
        position: relative;
        object-fit: cover;
        height: 100%;
        width: 100%;
        border-radius: var(--medium-border-radius);
    }
}

.cart-product-panel {
    overflow: hidden hidden;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    gap: var(--small-spacing) 0;
    position: relative;
    flex: 1 1 0;
}

.cart-product-panel-brand {
    overflow: hidden hidden;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    gap: var(--small-spacing) var(--small-spacing);
    position: relative;
    height: auto;
    width: 100%;
    color: var(--primary-text-color);
    font-size: var(--medium-font-size);
    font-weight: var(--large-font-weight);
    font-family: var(--primary-font-family);
    user-select: text;
}

.cart-product-panel-brand-strength {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    gap: 0 var(--tiny-spacing);
    padding: var(--tiny-spacing) var(--small-spacing);
    position: relative;
    height: auto;
    width: auto;
    font-size: var(--small-font-size);
    font-weight: var(--large-font-weight);
    font-family: var(--primary-font-family);
    border-radius: var(--small-border-radius);
    user-select: none;

    &.insane {
        color: rgba(220, 40, 220, 1);
        background-color: rgba(220, 40, 220, 0.2);
    }

    &.strong {
        color: rgba(240, 70, 40, 1);
        background-color: rgba(240, 70, 40, 0.2);
    }

    &.default {
        color: rgba(240, 140, 40, 1);
        background-color: rgba(240, 140, 40, 0.2);
    }

    &.low {
        color: rgba(70, 240, 40, 1);
        background-color: rgba(70, 240, 40, 0.2);
    }
}

.cart-product-panel-brand-strength-type {
    text-transform: uppercase;
}

.cart-product-panel-name {
    overflow: hidden hidden;
    display: -webkit-box;
    position: relative;
    height: auto;
    width: 100%;
    color: var(--secondary-text-color);
    font-size: var(--medium-font-size);
    font-weight: var(--primary-font-weight);
    font-family: var(--primary-font-family);
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    user-select: text;
}

.cart-product-panel-price {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: var(--medium-spacing) var(--medium-spacing);
    margin-top: auto;
    position: relative;
    height: auto;
    width: 100%;
}

.cart-product-panel-price-value {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: end;
    gap: 0 var(--tiny-spacing);
    position: relative;
    height: auto;
    width: auto;
    color: var(--primary-text-color);
    font-size: var(--primary-font-size);
    font-weight: var(--huge-font-weight);
    font-family: var(--primary-font-family);
}

.cart-product-panel-price-value-currency {
    box-sizing: border-box;
    padding-bottom: 1px;
    position: relative;
    height: auto;
    width: 100%;
    color: var(--secondary-text-color);
    font-size: var(--medium-font-size);
    font-weight: var(--huge-font-weight);
    font-family: var(--primary-font-family);
}

.cart-product-panel-price-actions {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: start;
    align-items: center;
    gap: 0 var(--small-spacing);
    margin-left: auto;
    position: relative;
    height: auto;
    width: auto;
}

.cart-product-panel-price-quantity {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    gap: 0 var(--tiny-spacing);
    padding: var(--small-spacing) var(--small-spacing);
    margin-left: auto;
    position: relative;
    height: auto;
    width: auto;
    background-color: var(--element-transparent-background-color);
    border-radius: var(--small-border-radius);
}

.cart-product-panel-price-quantity-remove {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 32px;
    width: 32px;
    color: var(--secondary-text-color);
    font-size: var(--medium-font-size);
    border-radius: var(--small-border-radius);
    cursor: pointer;
    transition: 0.1s;

    &.disabled {
        color: var(--additional-text-color);
        background-color: var(--element-transparent-background-color);
        transition: 0.1s;
    }
}

.cart-product-panel-price-quantity-value {
    position: relative;
    height: 32px;
    width: 32px;
    text-align: center;
    color: var(--primary-text-color);
    font-size: var(--primary-font-size);
    font-weight: var(--huge-font-weight);
    font-family: var(--primary-font-family);
    background-color: transparent;
    border-radius: var(--small-border-radius);
    border: none;
    outline: none;
}

.cart-product-panel-price-quantity-add {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 32px;
    width: 32px;
    color: var(--secondary-text-color);
    font-size: var(--medium-font-size);
    border-radius: var(--small-border-radius);
    cursor: pointer;
    transition: 0.1s;

    &.disabled {
        color: var(--additional-text-color);
        background-color: var(--element-transparent-background-color);
        transition: 0.1s;
    }
}

.cart-product-panel-price-remove {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: var(--small-spacing) var(--small-spacing);
    position: relative;
    height: 32px;
    width: 32px;
    color: var(--secondary-text-color);
    font-size: var(--medium-font-size);
    border-radius: var(--small-border-radius);
    background-color: var(--element-transparent-background-color);
    cursor: pointer;
}